import { createBrowserRouter } from "react-router-dom";
import App from "../App";
import {
  Settings,
  Prices,
  Goods
} from "../views";
export const router = createBrowserRouter([
  {
    path: "/",
    element: <App/>,
    errorElement: <p>Error</p>,
    children: [
      {
        path: "/",
        element: <p>Home</p>,
      },
      {
        path: "/prices",
        element: <Prices/>,
      },
      {
        path: "/goods",
        element: <Goods/>,
      },
      {
        path: "/settings",
        element: <Settings/>,
      }
    ]
  }
]);