import { createSlice } from '@reduxjs/toolkit';

const initialState: States.options = {
  promToken: '',
  fieldNameForPromId: '',
  groupIdForNewProducts: null,
  storeId: null,
  firmId: null,
  currencyObj: {},
  currencyArr: [],
};
export const optionsSlice = createSlice({
  name: 'options',
  initialState,
  reducers: {
    update(state, { payload }) {
      Object.assign(state, payload);
    },
    logout(state) {
      Object.assign(state, initialState);
    }
  }
});
