import { createSlice } from "@reduxjs/toolkit";

const initialState: States.settings = {
  lang: "ua",
  user: {},
  theme: "dark",
  alert: {
    show: false,
    msg: "",
    type: "success",
  }
};
export const settingsSlice = createSlice({
  name: "settings",
  initialState,
  reducers: {
    update(state, { payload }) {
      Object.assign(state, payload);
    },
    setAlert(state, { payload }) {
      Object.assign(state.alert, {
        show: true,
        ...payload
      });
    },
    setAlertError(state, { payload }) {
      Object.assign(state.alert, {
        show: true,
        msg: payload,
        type: "error",
      });
    },
    offAlert(state) {
      Object.assign(state.alert, {
        show: false,
      });
    }
  }
});
