import {
  Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  SelectChangeEvent,
  TextField,
  Typography
} from "@mui/material";
import { t, tField } from "../utils";
import { language } from "../common/language";
import { storeSelector, actions } from "../config/redux";
import { useDispatch } from "react-redux";
import LoadingButton from "@mui/lab/LoadingButton";
import { useEffect, useState } from "react";
import { Get, OpenPost, Put } from "../config/server";
import { BACKEND_URL } from "../common/constants";

interface HeandlerSearchProps {
  url: string;
  value: {
    value: string;
    loading: boolean;
    info: string;
  };
  setValue: (value: any) => void;
  msgSuccess: string;
  msgNotFound: string;
}

function Settings() {
  const dispatch = useDispatch();
  const { lang } = storeSelector(({ settings }) => settings);
  const { promToken, fieldNameForPromId, groupIdForNewProducts, storeId, firmId } = storeSelector(({ options }) => options);
  const { id } = storeSelector(({ auth }) => auth);
  const [loading, setLoading] = useState(false);
  const [textFields, setTextFields] = useState({
    promToken,
    fieldNameForPromId,
    groupIdForNewProducts,
    storeId,
    firmId,
  });
  const [searchGroupByName, setSearchGroupByName] = useState({
    value: '',
    loading: false,
    info: '',
  });
  const [searchStoreByName, setSearchStoreByName] = useState({
    value: '',
    loading: false,
    info: '',
  });
  const [searchFirmByName, setSearchFirmByName] = useState({
    value: '',
    loading: false,
    info: '',
  });
  const handleChangeLang = (e: SelectChangeEvent<any>) => {
    dispatch(actions.settings.update({ lang: e.target.value }));
  }
  const handleChange = (e: any) => {
    setTextFields({
      ...textFields,
      [e.target.name]: e.target.type === "number" ? +e.target.value : e.target.value
    });
  }
  const handleChangeSearchGroupByName = (e: any) => {
    setSearchGroupByName({
      ...searchGroupByName,
      value: e.target.value,
    });
  }
  const handleChangeSearchStoreByName = (e: any) => {
    setSearchStoreByName({
      ...searchStoreByName,
      value: e.target.value,
    });
  }
  const handleChangeSearchFirmByName = (e: any) => {
    setSearchFirmByName({
      ...searchFirmByName,
      value: e.target.value,
    });
  }
  const handleSearch = ({url, value, setValue, msgSuccess, msgNotFound}: HeandlerSearchProps ) => {
    setValue({
      ...value,
      loading: true,
    })
    OpenPost(`${BACKEND_URL}${url}`, { name: value.value }, (res: [{NUM: number}]) => {
      setValue({
        ...value,
        loading: false,
        info: res.length ? `${msgSuccess}${res.map((item) => item.NUM).join(', ')}` : msgNotFound,
      });
    });
  }
  const groupFind = () => {
    handleSearch({
      url: 'group/find',
      value: searchGroupByName,
      setValue: setSearchGroupByName,
      msgSuccess: t(tField.infoIdGroup),
      msgNotFound: t(tField.infoIdGroupNotFound),
    })
  }
  const storeFind = () => {
    handleSearch({
      url: 'store/find',
      value: searchStoreByName,
      setValue: setSearchStoreByName,
      msgSuccess: t(tField.infoIdStore),
      msgNotFound: t(tField.infoIdStoreNotFound),
    })
  }
  const firmFind = () => {
    handleSearch({
      url: 'firm/find',
      value: searchFirmByName,
      setValue: setSearchFirmByName,
      msgSuccess: t(tField.infoIdFirm),
      msgNotFound: t(tField.infoIdFirmNotFound),
    })
  }
  const handleLogout = () => {
    dispatch(actions.auth.logout());
  }
  const handleSave = () => {
    setLoading(true);
    Put(['options', id], textFields, () => {
      setLoading(false);
      dispatch(actions.options.update(textFields));
      dispatch(actions.settings.setAlert({
        show: true,
        type: 'success',
        msg: t(tField.infoSettingsSaved),
      }));
    });
  }
  useEffect(() => {
    Get(['options', id], (res) => {
      dispatch(actions.options.update(res));
    });
  }, [])
  useEffect(() => {
    setTextFields({
      promToken,
      fieldNameForPromId,
      groupIdForNewProducts,
      storeId,
      firmId,
    });
  }, [promToken, fieldNameForPromId, groupIdForNewProducts, storeId, firmId])
  return (
    <Box sx={{ minWidth: 120, "& button": { my: 1 }, "& .MuiTextField-root": { my: 1 } }}>
      <form>
        <FormControl fullWidth>
          <InputLabel id="language-select-label">{t(tField.language)}</InputLabel>
          <Select
            labelId="language-select-label"
            id="language-select"
            value={lang}
            label={t(tField.language)}
            onChange={handleChangeLang}
            name="language"
          >
            {language.map((item, index) => (
              <MenuItem key={index} value={item.value}>{item.label}</MenuItem>
            ))}
          </Select>
          <Button variant="outlined" onClick={handleLogout}>{t(tField.btnLogout)}</Button>
          <TextField
            id="prom-id"
            label={t(tField.inputPromToken)}
            name="promToken"
            onChange={handleChange}
            size="small"
            type="text"
            value={textFields.promToken}
            disabled
          />
          <TextField
            id="field-name-for-prom-id"
            label={t(tField.inputFieldNameForPromId)}
            name="fieldNameForPromId"
            onChange={handleChange}
            size="small"
            type="text"
            value={textFields.fieldNameForPromId}
          />
          <TextField
            id="group-id-for-new-products"
            label={t(tField.inputGroupIdForNewProducts)}
            name="groupIdForNewProducts"
            onChange={handleChange}
            size="small"
            type="number"
            value={textFields.groupIdForNewProducts}
          />
          <Box sx={{ display: 'flex', justifyContent: 'flex-start', "& button": { m: 1 } }}>
            <TextField
              id="search-group-by-name"
              label={t(tField.inputSearchGroupByName)}
              name="searchGroupByName"
              onChange={handleChangeSearchGroupByName}
              size="small"
              type="text"
              value={searchGroupByName.value}
            />
            <LoadingButton
              loading={searchGroupByName.loading}
              onClick={groupFind}
              variant="outlined"
              size="large"
              type="button"
            >
              {t(tField.btnSearch)}
            </LoadingButton>
            <Typography
              variant="body2"
              color="text.secondary"
              component="p"
              sx={{ alignSelf: "center" }}
            >
              {searchGroupByName.info}
            </Typography>
          </Box>
          <TextField
            id="group-id-for-new-products"
            label={t(tField.inputStoreId)}
            name="storeId"
            onChange={handleChange}
            size="small"
            type="number"
            value={textFields.storeId}
          />
          <Box sx={{ display: 'flex', justifyContent: 'flex-start', "& button": { m: 1 } }}>
            <TextField
              id="search-group-by-name"
              label={t(tField.inputSearchStoreByName)}
              name="searchGroupByName"
              onChange={handleChangeSearchStoreByName}
              size="small"
              type="text"
              value={searchStoreByName.value}
            />
            <LoadingButton
              loading={searchStoreByName.loading}
              onClick={storeFind}
              variant="outlined"
              size="large"
              type="button"
            >
              {t(tField.btnSearch)}
            </LoadingButton>
            <Typography
              variant="body2"
              color="text.secondary"
              component="p"
              sx={{ alignSelf: "center" }}
            >
              {searchStoreByName.info}
            </Typography>
          </Box>
          <TextField
            id="firm-id"
            label={t(tField.inputFirmId)}
            name="firmId"
            onChange={handleChange}
            size="small"
            type="number"
            value={textFields.firmId}
          />
          <Box sx={{ display: 'flex', justifyContent: 'flex-start', "& button": { m: 1 } }}>
            <TextField
              id="search-firm-by-name"
              label={t(tField.inputSearchFirmByName)}
              name="searchFirmByName"
              onChange={handleChangeSearchFirmByName}
              size="small"
              type="text"
              value={searchFirmByName.value}
            />
            <LoadingButton
              loading={searchFirmByName.loading}
              onClick={firmFind}
              variant="outlined"
              size="large"
              type="button"
            >
              {t(tField.btnSearch)}
            </LoadingButton>
            <Typography
              variant="body2"
              color="text.secondary"
              component="p"
              sx={{ alignSelf: "center" }}
            >
              {searchFirmByName.info}
            </Typography>
          </Box>
          <LoadingButton
            loading={loading}
            onClick={handleSave}
            variant="outlined"
            size="large"
            type="submit"
          >
            {t(tField.btnSave)}
          </LoadingButton>
        </FormControl>
      </form>
    </Box>
  )
}

export default Settings;
