import Axios from "axios";
import { API_URL } from "../../common/constants";
import { waiter } from "../../utils";
import store, { actions } from "../redux";
const Routes = {
  get: {
    ping: "ping",
    content: "content",
    allUsers: "auth/users/all",
    allPosts: "posts/all",
    allFiles: "files/all",
    allMails: "mails/all",
    allBackups: "backup/all",
    allAnalytics: "analytics/all",
    analyticsToday: "analytics/today",
    analyticsThisWeek: "analytics/this-week",
    analyticsThisMonth: "analytics/this-month",
    userId: "auth/users",
    contentId: "content",
    postId: "posts/id",
    postById: "posts/post-id",
    allProducts: "product/all",
    productId: "product/single",
    allReceipts: "receipt/all",
    receiptId: "receipt/id",
    receiptById: "receipt/order",
    allCarts: "cart/all",
    cartId: "cart/order",
    mailBy: "mails/by-type",
    allPostmen: "mails/all-postmen",
    storeSetup: "cart/store-setup",
    analytics: "analytics",
    options: "user/options",
    optionsByToken: "user/options-by-token",
  },
  post: {
    login: "auth/login",
    refresh: "auth/refresh",
    query: "db/query",
    userId: "auth/users",
    files: "",
    promProducts: "prom/products",
    promGroups: "prom/groups",
    promProductsByGroups: "prom/products-by-group-ids",
  },
  del: {
    userId: "auth/users",
    allContent: "content/all",
    contentId: "content/single",
    allPosts: "posts/all",
    postId: "posts/single",
    allMails: "mails/all",
    mailId: "mails/single",
    fileId: "files/single",
    productId: "product/single",
    receiptId: "receipt",
    cartId: "cart/order",
    postmanId: "mails/delete-postman",
  },
  put: {
    userId: "auth/users",
    postId: "posts",
    mailId: "mails",
    contentId: "content",
    productId: "product/single",
    receiptId: "receipt",
    cartDetailsId: "cart/order-details",
    cartDetailsInstalmentId: "cart/order-product-instalment",
    cartDetailsPromoId: "cart/order-product-promo",
    cartProductId: "cart/order-product",
    options: "user/options",
  },
};
const delay = 2;
export const api = Axios.create({
  baseURL: API_URL,
  headers: {
    "Content-Type": "application/json",
  },
});

export const openApi = Axios.create({
  headers: {
    "Content-Type": "application/json",
  },
});

export const OpenPost: ServerAPI.OpenReq = async (route, input, fn = () => {}, fail = () => {}, opt) => {
  try {
    const { data, status } = await openApi.post(route, input, opt);
    if (status < 300) {
      fn(data);
      return data;
    }
  } catch (err) {
    fail(err);
    return null;
  }
};

const getUser = () => {
  return store.getState().auth;
};

const refresher = {
  isRefreshing: false,
  status: "none",
};

const waitRefresher = async () => {
  if (refresher.isRefreshing) {
    await waiter(delay);
    if (refresher.status === "pending") {
      await waiter(delay);
    }
  }
};

const refresh = (isRef: boolean, ref: "pending" | "none") => {
  refresher.isRefreshing = isRef;
  refresher.status = ref;
};

export const setToken = (token: string) => (api.defaults.headers.common.Authorization = `Bearer ${token}`);
export const Get: ServerAPI.GetReq = async (key, fn = () => {}, fail = () => {}) => {
  await waitRefresher();
  if (refresher.isRefreshing) {
    fail(null);
    return null;
  }
  try {
    const route = typeof key === "string" ? Routes.get[key] : [Routes.get[key[0]], key[1]].join("/");
    const { data, status } = await api.get(route);
    if (status < 300) {
      fn(data);
      return data;
    }
  } catch (err) {
    fail(err);
    return null;
  }
};

export const Post: ServerAPI.PostReq = async (key, input, fn = () => {}, fail = () => {}, opt) => {
  await waitRefresher();
  if (refresher.isRefreshing) {
    fail(null);
    return null;
  }
  try {
    const route = typeof key === "string" ? Routes.post[key] : [Routes.post[key[0]], key[1]].join("/");
    const { data, status } = await api.post(route, input, opt);
    if (status < 300) {
      if (key === "login" || key === "refresh") {
        setToken(data.accessToken);
      }
      fn(data);
      return data;
    }
  } catch (err) {
    fail(err);
    return null;
  }
};

export const Put: ServerAPI.PutReq = async ([key, Id], input, fn = () => {}, fail = () => {}) => {
  await waitRefresher();
  if (refresher.isRefreshing) {
    fail(null);
    return null;
  }
  try {
    const { data, status } = await api.put([Routes.put[key], Id].join("/"), input);
    if (status < 300) {
      fn(data);
      return data;
    }
  } catch (err) {
    fail(err);
    return null;
  }
};

export const Del: ServerAPI.DelReq = async ([key, Id], fn = () => {}, fail = () => {}) => {
  await waitRefresher();
  if (refresher.isRefreshing) {
    fail(null);
    return null;
  }
  try {
    const { data, status } = await api.delete([Routes.del[key], Id].join("/"));
    if (status < 300) {
      fn(data);
      return data;
    }
  } catch (err) {
    fail(err);
    return null;
  }
};

export const Upload: ServerAPI.FileReq = async (input, fn = () => {}, fail = () => {}, opt) => {
  await waitRefresher();
  if (refresher.isRefreshing) {
    fail(null);
    return null;
  }
  try {
    const buffer = new FormData();
    buffer.append("file", input);
    const { data, status } = await api.post(Routes.post.files, buffer, opt);
    if (status < 300) {
      fn(data);
      return data;
    }
  } catch (err) {
    fail(err);
    return null;
  }
};

const notification = document.getElementById("notification-stack");
function notify(m: string, t: string) {
  if (!notification) return 0;
  notification.innerHTML += `<div class="notification ${t}" ><p>${m}</p></div>`;
  setTimeout(() => {
    notification.firstElementChild?.remove();
  }, 4000);
}
// Settings
export const setup: ServerAPI.Setup = (token = "", notifier) => {
  if (!token) return null;
  setToken(token);
  api.interceptors.request.use(async (config) => {
    return config;
  });
  api.interceptors.response.use(
    (response) => {
      if (notifier) notify(response?.config?.url || "success", "bs");
      return response;
    },
    async (error) => {
      if (notifier) notify(`[${error?.response?.status}] ${error?.config?.url} : ${error?.response?.data?.message}` || "fail", "bf");
      const newConfig = error.config;
      if (error?.response?.status === 403 && error.response.config.method !== "get") {
        if (notifier) notify("You don't have the permission to do that", "bs");
      }
      if (error?.response?.status === 401) {
        if (refresher.isRefreshing) {
          await waiter(delay);
          newConfig._retry = true;
          return new Promise(async (resolve, reject) => {
            try {
              resolve(api(newConfig));
            } catch (err) {
              reject(err);
            }
          });
        }
        refresh(true, "pending");
        const { refreshToken } = getUser();

        return new Promise(async (resolve, reject) => {
          try {
            const { data, status } = await api.post(Routes.post.refresh, { token: refreshToken });
            if (status < 300) {
              setToken(data.accessToken);
              store.dispatch(actions.auth.update(data));
              refresh(false, "none");
              newConfig.headers.Authorization = `Bearer ${data.accessToken}`;
              resolve(api(newConfig));
            }
          } catch (err) {
            refresh(false, "none");
            store.dispatch(actions.auth.update({ logged: false }));
            reject(err);
          }
        });
      }
      return Promise.reject(error.response);
    }
  );
};
