import {
  combineReducers,
  configureStore,
  getDefaultMiddleware,
  type AnyAction,
  type ThunkDispatch
} from '@reduxjs/toolkit';
import { useSelector, type TypedUseSelectorHook } from 'react-redux';
import logger from 'redux-logger';
import { FLUSH, PAUSE, PERSIST, persistReducer, PURGE, REGISTER, REHYDRATE } from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import { activeLogger } from '../../common/constants';
import { settingsSlice } from './settings.slice';
import { authSlice } from './auth.slice';
import { optionsSlice } from "./options.slice";

const persistConfig = {
  key: 'apxu-sync-v4',
  storage,
  blacklist: [],
};
export const combinedReducers = combineReducers({
  settings: settingsSlice.reducer,
  auth: authSlice.reducer,
  options: optionsSlice.reducer,
});
const persistedReducer = persistReducer(persistConfig, combinedReducers);
const logs = activeLogger ? [logger] : [];
const store = configureStore({
  reducer: persistedReducer,
  middleware: [
    ...getDefaultMiddleware({
      serializableCheck: {
        ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
      },
    }),
    ...logs,
  ],
});
export default store;
export type RootState = ReturnType<typeof combinedReducers>;
export const actions = {
  settings: settingsSlice.actions,
  auth: authSlice.actions,
  options: optionsSlice.actions,
};
export type TD = ThunkDispatch<RootState, any, AnyAction>;
export const storeSelector: TypedUseSelectorHook<RootState> = useSelector;