import { createSlice } from '@reduxjs/toolkit';

const initialState: States.auth = {
  accessToken: '',
  refreshToken: '',
  id: '',
};
export const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    update(state, { payload }) {
      Object.assign(state, payload);
    },
    logout(state) {
      Object.assign(state, initialState);
    }
  }
});